import hex_sha1 from "sha1";
import uniLoadMore from "@/components/uni-load-more.vue";
import {ChangePageTitle} from '../../title.js'
export default {
	data() {
		return {
			back1: true,
			title: '客户名称',
			accessToken: '64e9275246c6545a3b5d3ad060fe4c516f44d556',
			appsercert: 'MTExMTEx',
			page: 1,
			list: [],
			status: 'more',
			isShow: false,
		}
	},
	components: {
		uniLoadMore
	},
	methods: {
		getlist() {
			var signStr =
				"access-token=" +
				this.accessToken +
				"&appsercert=" +
				this.appsercert +
				"&page=" +
				this.page;
			// console.log(signStr)
			this.sha = hex_sha1(signStr);
			console.log(this.sha)
			this.$axios(this.$baseUrl.customlist,{
				'access-token': this.accessToken,
				sign: this.sha,
				page: this.page
			})
			.then(res=>{
				console.log(res)
				uni.hideLoading()
				if (res.data.items.length == 0) {
					this.status = "noMore";
				} else {
					console.log(res.data);
					this.list = this.list.concat(res.data.items);
					for (var i = 0; i < this.list.length; i++) {
						this.$set(this.list[i], 'slide_x', 0)
					}
					this.isShow = true
				}
			},(error)=>{
				console.log(error)
			})
		},
		xuanze(name,id){
			console.log(name)
			console.log(id)
			let obj = uni.getStorageSync('list')
			console.log(obj)
			obj.kehuname = name
			obj.kehu_id = id
			uni.setStorageSync('list',obj)
			uni.navigateTo({
				url:'/pages/addperson/index'
			})
		}
	},
	onReachBottom() {
		this.status = "loading";
		console.log("我被拉到底部了");
		setTimeout(() => {
			this.page++;
			console.log(this.page);
			this.getlist();
		}, 2000)
	},
	onLoad() {
		this.getlist()
	},
	onShow() {
		document.title = '客户名称'
		ChangePageTitle('客户名称')
	}
}
